@use "../abstract/breakpoints" as *;
@use "../abstract/colors" as *;
@use "../abstract/fonts" as *;
@use "../abstract/functions" as *;
@use "../abstract/mixins" as *;


.header{
    position: fixed;
    top:0;
    width: 100%;
    padding-block:em(16);
    @include transition(all, 0.4s, ease);

    &.sticky{
        background-color:$clr-primary;
        color:$clr-white-100;
        z-index: 3;
        border-bottom: 1px solid $clr-grey-100;
        top:-1px;
    }
    &__wrapper{
        @include flex-space-between;
    }
    &__mobile{
        position: fixed;
        inset:0;
        z-index: 99;
        width: rem(420);
        max-width: 100%;
        height: 100vh;
        background: $clr-primary;
        color: $clr-white-100;
        @include flex-center-vertical;
        flex-direction: column;
        top: 53px;
        opacity: 0;
        visibility: hidden;
        @include transition(0.3s, ease-out);
        @include transform-x(-100%);
        
        @include breakpoint-sm{
            top: 60px;
        }
        @include breakpoint-md{
            top: 62px;
        }
        @include breakpoint-lg{
            top: 66px;
        }
        &.open{
            opacity: 1;
            visibility: visible;
            @include transition(transform, 0.3s, ease-out);
            @include transform-x(0);
        }
    
        &-list{
            width: 100%;
            @include flex-center;
            flex-direction: column;
            gap:rem(40);
            margin-top:rem(100);
        }
    
        &-link{
            color: $clr-white-100;
            font-size: rem(18);
        }
    }
    &__logo{
        @include flex-center;
        img{
            display: flex;
        }
    }
}

@media only screen and (max-width:36em){
    .header__logo{
        img{
            width: rem(160);
        }
    }
}

// Header Transparent
.header-tranparent{
    background:$clr-white-50;
}

